import http from './index'
import store from '../store'
import {
    SET_INFO
} from '../store/mutation-type'
import router from '../router'
import {
    Message
} from 'element-ui'
export default {
    //登录
    login: (data, message) => {
        http('login', data, 'POST')
            .then(({
                meta: {
                    msg
                },
                data: {
                    token,
                    username
                }
            }) => {
                message({
                    type: 'success',
                    message: msg
                })
                store.commit(SET_INFO, {
                    token,
                    username
                })
                router.push({
                    name: 'Home'
                })
            }).catch(err => {
                message({
                    type: 'error',
                    message: err
                })
            })
    },
    // 获取左侧权限菜单
    async getMenus() {
        const res = await http('menus', null, 'GET')
        return res.data;
    },
    //获取用户列表
    async getUsers(params) {
        const res = await http('users', null, 'GET', params)
        return res.data
    },
    //修改用户状态
    async updateState({
        id,
        mg_state
    }) {
        const res = await http(`users/${id}/state/${mg_state}`, null, 'PUT')
        Message({
            type: 'success',
            message: res.meta.msg
        })
        return res.data
    },
}